.label {
  display: block;
  margin-bottom: 20px;
  font-size: 20px;
}

.input {
  display: block;
  /* margin-left: auto;
  margin-right: auto; */
  padding: 10px;
  border: 1px solid black;
  border-radius: 4px;
  cursor: pointer;
  font-size: 15px;
}

.input:focus {
  outline-color: blue;
}

.btn {
  padding: 10px 20px;
  background-color: blue;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border: none;
  outline: none;
  opacity: 0.8;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  cursor: pointer;
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
}

.btn:hover {
  opacity: 1;
}
